import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Settings, X, Sun, Moon, Palette, Check } from 'lucide-react';
import { useTheme } from '../../context/ThemeContext';

const themes = [
  { id: 'light', name: 'Light', icon: Sun },
  { id: 'dark', name: 'Dark', icon: Moon },
  { id: 'solarized', name: 'Solarized', icon: Palette },
];

const colors = [
  { id: 'blue', value: '#3B82F6' },
  { id: 'indigo', value: '#6366F1' },
  { id: 'purple', value: '#8B5CF6' },
  { id: 'pink', value: '#EC4899' },
  { id: 'red', value: '#EF4444' },
  { id: 'orange', value: '#F97316' },
  { id: 'green', value: '#22C55E' },
  { id: 'teal', value: '#14B8A6' },
];

const ThemeCustomizer = () => {
  const [isOpen, setIsOpen] = useState(false);
  const { theme, setTheme, primaryColor, setPrimaryColor } = useTheme();

  // Load saved preferences
  useEffect(() => {
    const savedTheme = localStorage.getItem('theme');
    const savedColor = localStorage.getItem('primaryColor');
    
    if (savedTheme) setTheme(savedTheme);
    if (savedColor) setPrimaryColor(savedColor);
  }, []);

  // Save preferences
  useEffect(() => {
    localStorage.setItem('theme', theme);
    localStorage.setItem('primaryColor', primaryColor);

    // Update CSS variables
    document.documentElement.style.setProperty('--primary-color', primaryColor);
    document.documentElement.className = theme;
  }, [theme, primaryColor]);

  return (
    <>
      {/* Toggle Button */}
      <button
        onClick={() => setIsOpen(true)}
        className="fixed right-4 bottom-4 p-3 bg-white dark:bg-gray-800 rounded-full shadow-lg hover:shadow-xl transition-shadow"
        aria-label="Customize theme"
      >
        <Settings className="w-6 h-6 text-gray-600 dark:text-gray-300" />
      </button>

      {/* Backdrop */}
      <AnimatePresence>
        {isOpen && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            onClick={() => setIsOpen(false)}
            className="fixed inset-0 bg-black bg-opacity-50 z-40"
          />
        )}
      </AnimatePresence>

      {/* Drawer */}
      <AnimatePresence>
        {isOpen && (
          <motion.div
            initial={{ x: '100%' }}
            animate={{ x: 0 }}
            exit={{ x: '100%' }}
            transition={{ type: 'spring', damping: 20 }}
            className="fixed right-0 top-0 h-full w-80 bg-white dark:bg-gray-800 shadow-2xl z-50"
          >
            {/* Header */}
            <div className="flex justify-between items-center p-4 border-b border-gray-200 dark:border-gray-700">
              <h2 className="text-lg font-semibold text-gray-900 dark:text-white">
                Customize Theme
              </h2>
              <button
                onClick={() => setIsOpen(false)}
                className="p-2 hover:bg-gray-100 dark:hover:bg-gray-700 rounded-full"
              >
                <X className="w-5 h-5 text-gray-500 dark:text-gray-400" />
              </button>
            </div>

            {/* Content */}
            <div className="p-4 space-y-6">
              {/* Theme Selection */}
              <div>
                <h3 className="text-sm font-medium text-gray-900 dark:text-white mb-3">
                  Theme
                </h3>
                <div className="grid grid-cols-3 gap-3">
                  {themes.map(({ id, name, icon: Icon }) => (
                    <button
                      key={id}
                      onClick={() => setTheme(id)}
                      className={`
                        flex flex-col items-center justify-center p-3 rounded-lg
                        border-2 transition-colors
                        ${theme === id
                          ? 'border-primary-500 bg-primary-50 dark:bg-primary-900/20'
                          : 'border-gray-200 dark:border-gray-700 hover:border-gray-300 dark:hover:border-gray-600'
                        }
                      `}
                    >
                      <Icon className={`
                        w-6 h-6 mb-1
                        ${theme === id
                          ? 'text-primary-500'
                          : 'text-gray-500 dark:text-gray-400'
                        }
                      `} />
                      <span className={`
                        text-sm font-medium
                        ${theme === id
                          ? 'text-primary-600 dark:text-primary-400'
                          : 'text-gray-600 dark:text-gray-300'
                        }
                      `}>
                        {name}
                      </span>
                    </button>
                  ))}
                </div>
              </div>

              {/* Color Selection */}
              <div>
                <h3 className="text-sm font-medium text-gray-900 dark:text-white mb-3">
                  Primary Color
                </h3>
                <div className="grid grid-cols-4 gap-3">
                  {colors.map(({ id, value }) => (
                    <button
                      key={id}
                      onClick={() => setPrimaryColor(value)}
                      className="relative aspect-square rounded-full"
                      style={{ backgroundColor: value }}
                    >
                      {primaryColor === value && (
                        <Check className="absolute inset-0 m-auto w-4 h-4 text-white" />
                      )}
                    </button>
                  ))}
                </div>
              </div>

              {/* Preview */}
              <div className="border border-gray-200 dark:border-gray-700 rounded-lg p-4">
                <h3 className="text-sm font-medium text-gray-900 dark:text-white mb-3">
                  Preview
                </h3>
                <div className="space-y-3">
                  <button
                    className="w-full px-4 py-2 bg-primary-500 hover:bg-primary-600 text-white rounded-lg"
                  >
                    Primary Button
                  </button>
                  <div className="flex items-center space-x-2">
                    <div className="w-4 h-4 rounded-full bg-primary-500" />
                    <span className="text-sm text-primary-500">Primary Text</span>
                  </div>
                </div>
              </div>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </>
  );
};

export default ThemeCustomizer; 