import React, { Fragment, useState, useEffect } from 'react';
import { Dialog, Combobox, Transition } from '@headlessui/react';
import { useNavigate } from 'react-router-dom';
import { motion, AnimatePresence } from 'framer-motion';
import { Search, Command, Settings, Home, Calendar, BarChart, Users } from 'lucide-react';
import { useTheme } from '../context/ThemeContext';

// Define the command item type
type CommandItem = {
  id: string;
  name: string;
  description: string;
  icon: JSX.Element;
  path?: string;
  action?: () => void;
};

// Command items for navigation and actions
const commands: CommandItem[] = [
  {
    id: 'home',
    name: 'Go to Dashboard',
    description: 'Return to the main dashboard view',
    icon: <Home className="w-5 h-5" />,
    path: '/'
  },
  {
    id: 'calendar',
    name: 'Calendar',
    description: 'View and manage your schedule',
    icon: <Calendar className="w-5 h-5" />,
    path: '/calendar'
  },
  {
    id: 'analytics',
    name: 'Analytics',
    description: 'View detailed analytics and reports',
    icon: <BarChart className="w-5 h-5" />,
    path: '/analytics'
  },
  {
    id: 'users',
    name: 'Users',
    description: 'Manage user accounts and permissions',
    icon: <Users className="w-5 h-5" />,
    path: '/users'
  },
  {
    id: 'settings',
    name: 'Settings',
    description: 'Configure app settings and preferences',
    icon: <Settings className="w-5 h-5" />,
    path: '/settings'
  },
];

export default function CommandPalette() {
  const [isOpen, setIsOpen] = useState(false);
  const [query, setQuery] = useState('');
  const navigate = useNavigate();
  const { isDarkMode } = useTheme();

  // Filter commands based on search query
  const filteredCommands = query === ''
    ? commands
    : commands.filter((command) => {
        const searchContent = `${command.name} ${command.description}`.toLowerCase();
        return searchContent.includes(query.toLowerCase());
      });

  // Handle keyboard shortcut
  useEffect(() => {
    const handleKeyDown = (event: KeyboardEvent) => {
      if ((event.metaKey || event.ctrlKey) && event.key === 'k') {
        event.preventDefault();
        setIsOpen((prev) => !prev);
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, []);

  // Handle command selection
  const handleSelect = (command: CommandItem) => {
    setIsOpen(false);
    if (command.path) {
      navigate(command.path);
    } else if (command.action) {
      command.action();
    }
  };

  return (
    <Transition.Root show={isOpen} as={Fragment}>
      <Dialog
        as="div"
        className="fixed inset-0 z-50 overflow-y-auto p-4 sm:p-6 md:p-20"
        onClose={setIsOpen}
      >
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100"
          leaveTo="opacity-0"
        >
          <Dialog.Overlay className="fixed inset-0 bg-gray-500/75 dark:bg-gray-900/90 transition-opacity" />
        </Transition.Child>

        <Transition.Child
          as={Fragment}
          enter="ease-out duration-300"
          enterFrom="opacity-0 scale-95"
          enterTo="opacity-100 scale-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100 scale-100"
          leaveTo="opacity-0 scale-95"
        >
          <Dialog.Panel className="mx-auto max-w-2xl transform divide-y divide-gray-100 dark:divide-gray-700 overflow-hidden rounded-xl bg-white dark:bg-gray-800 shadow-2xl ring-1 ring-black ring-opacity-5 transition-all">
            <Combobox onChange={handleSelect}>
              <div className="relative">
                <Search
                  className="pointer-events-none absolute left-4 top-3.5 h-5 w-5 text-gray-400 dark:text-gray-500"
                  aria-hidden="true"
                />
                <Command
                  className="pointer-events-none absolute right-4 top-3.5 h-5 w-5 text-gray-400 dark:text-gray-500"
                  aria-hidden="true"
                />
                <Combobox.Input
                  className="h-12 w-full border-0 bg-transparent pl-11 pr-11 text-gray-900 dark:text-gray-100 placeholder:text-gray-400 dark:placeholder:text-gray-500 focus:ring-0 sm:text-sm"
                  placeholder="Search commands..."
                  onChange={(event) => setQuery(event.target.value)}
                />
              </div>

              <Combobox.Options static className="max-h-96 scroll-py-3 overflow-y-auto p-3">
                {filteredCommands.length > 0 && (
                  <AnimatePresence>
                    {filteredCommands.map((command) => (
                      <Combobox.Option
                        key={command.id}
                        value={command}
                        className={({ active }) =>
                          `flex cursor-pointer select-none rounded-xl p-3 ${
                            active 
                              ? 'bg-primary-50 dark:bg-primary-900/20 text-primary-900 dark:text-primary-100' 
                              : 'text-gray-900 dark:text-gray-100'
                          }`
                        }
                      >
                        {({ active }) => (
                          <motion.div
                            initial={{ opacity: 0, y: 5 }}
                            animate={{ opacity: 1, y: 0 }}
                            exit={{ opacity: 0, y: -5 }}
                            className="flex items-center gap-3"
                          >
                            <span className={`flex-shrink-0 ${
                              active 
                                ? 'text-primary-600 dark:text-primary-400' 
                                : 'text-gray-400 dark:text-gray-500'
                            }`}>
                              {command.icon}
                            </span>
                            <div>
                              <p className={`text-sm font-medium ${
                                active 
                                  ? 'text-primary-900 dark:text-primary-100' 
                                  : 'text-gray-900 dark:text-gray-100'
                              }`}>
                                {command.name}
                              </p>
                              <p className={`text-sm ${
                                active 
                                  ? 'text-primary-700 dark:text-primary-300' 
                                  : 'text-gray-500 dark:text-gray-400'
                              }`}>
                                {command.description}
                              </p>
                            </div>
                          </motion.div>
                        )}
                      </Combobox.Option>
                    ))}
                  </AnimatePresence>
                )}

                {filteredCommands.length === 0 && (
                  <div className="py-14 px-6 text-center text-sm sm:px-14">
                    <Search
                      className="mx-auto h-6 w-6 text-gray-400 dark:text-gray-500"
                      aria-hidden="true"
                    />
                    <p className="mt-4 font-semibold text-gray-900 dark:text-gray-100">
                      No commands found
                    </p>
                    <p className="mt-2 text-gray-500 dark:text-gray-400">
                      We couldn't find anything with that term. Please try again.
                    </p>
                  </div>
                )}
              </Combobox.Options>
            </Combobox>
          </Dialog.Panel>
        </Transition.Child>
      </Dialog>
    </Transition.Root>
  );
} 